<template>
	<div id="responsible-gaming">
		<Transition :name="transition">
			<RouterView />
		</Transition>
	</div>
</template>

<script>
// import './assets/scss/default.scss'; // eslint-disable-line import/no-unassigned-import

export default {
	data() {
		return {
			transition: ''
		};
	},
	watch: {
		$route(to) {
			if (/(home|mate)/.test(to.name)) {
				this.transition = 't-page';
			} else {
				this.transition = 't-scale';
			}
		}
	}
};
</script>

<style>
.bg-pattern {
  background-image: url(https://www.responsiblegambling.nsw.gov.au/__data/assets/git_bridge/0009/271971/templates-2020/images/pattern-bg.svg);
}

.bg-pattern2 {
  background-image:url(https://www.responsiblegambling.nsw.gov.au/__data/assets/git_bridge/0009/271971/templates-2020/images/pattern-bg2.jpg);
}
</style>

<style scoped lang="scss">
.t-scale-leave-active,
.t-scale-enter-active {
	transition: all 0.5s ease;
}

.t-scale-enter {
	opacity: 0;
	transform: scale(0);
}

.t-scale-enter-to {
	opacity: 1;
	transform: scale(1);
}

// Subpages
.t-page-leave-active {
	transition: all 0.3s ease;
}
.t-page-enter-active {
	transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

.t-page-enter,
.t-page-leave-to {
	opacity: 0.3;
}

.t-page-enter-to,
.t-page-leave {
	opacity: 1;
}
</style>
