<template>
	<div :class="[`game-start game-start--level_${level}`]">
		<div class="game-start-intro d-flex flex-column align-items-center">
			<div ref="mate" class="game-start__mate" />
			<div
				ref="card"
				class="game-start-card d-flex flex-column align-items-center justify-content-center align-self-center"
			>
				<div class="pb-1">
					<h3 ref="cardHeading" class="game-start__heading">
						Level {{ level }}
					</h3>
					<div class="d-inline-flex flex-column bet-result mr-2">
						<h4
							ref="cardBetValue"
							class="bet-result__heading bet-result__heading--green"
						>
							+${{ levelSettings.bet }}
						</h4>
						<ButtonGame
							ref="cardBet"
							class="bet-result__btn"
							size="small"
							color="green"
						>
							BET
						</ButtonGame>
					</div>
					<div class="d-inline-flex flex-column bet-result ml-2">
						<h4
							ref="cardBustValue"
							class="bet-result__heading bet-result__heading--red"
						>
							+${{ levelSettings.bust }}
						</h4>
						<ButtonGame
							ref="cardBust"
							class="bet-result__btn"
							size="small"
							color="red"
						>
							BUST
						</ButtonGame>
					</div>
				</div>
			</div>
			<Button
				ref="button"
				size="large"
				class="game-start__btn mt-4"
				@click.native="start()"
			>
				Start<template v-if="autoPlay.timer > 0">
					({{ autoPlay.timer }})
				</template>
			</Button>
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import anime from 'animejs';
import lottie from 'lottie-web';

import ButtonGame from '@/components/ButtonGame.vue';
import Button from '@/components/Button.vue';

export default {
	name: 'GameStepStart',
	components: { Button, ButtonGame },
	props: {},
	data() {
		return {
			autoPlay: {
				timer: 3,
				interval: null
			}
		};
	},
	computed: {
		...mapGetters(['level', 'score', 'levelSettings'])
	},
	mounted() {
		const self = this;

		this.animateSlideUp(this.$refs.card);
		this.animateSlideUp(this.$refs.button.$el, 200);
		this.animationCharacter();
		this.animateCard();

		this.autoPlay.interval = setInterval(() => {
			self.autoPlay.timer -= 1;
			if (self.autoPlay.timer <= 0) {
				self.start();
				clearInterval(self.autoPlay.interval);
			}
		}, 1000);
	},
	methods: {
		animateCard() {
			const title = this.$refs.cardHeading;
			title.style.opacity = 0;
			title.style.transform = 'scale(0)';
			anime({
				targets: title,
				scale: 1,
				opacity: 1,
				delay: 800,
				duration: 800
			});

			const buttonBet = this.$refs.cardBet.$el;
			buttonBet.style.opacity = 0;
			buttonBet.style.transform = 'scale(0)';
			anime({
				targets: buttonBet,
				scale: 1,
				opacity: 1,
				delay: 1100,
				duration: 800
			});

			const buttonBust = this.$refs.cardBust.$el;
			buttonBust.style.opacity = 0;
			buttonBust.style.transform = 'scale(0)';
			anime({
				targets: buttonBust,
				scale: 1,
				opacity: 1,
				delay: 1250,
				duration: 800
			});

			const { cardBetValue } = this.$refs;
			const { cardBustValue } = this.$refs;
			cardBetValue.style.opacity = 0;
			cardBetValue.style.transform = 'translateY(50px)';
			cardBustValue.style.opacity = 0;
			cardBustValue.style.transform = 'translateY(50px)';
			anime({
				targets: [cardBetValue, cardBustValue],
				translateY: 0,
				opacity: 1,
				delay: 1400,
				duration: 1200
			});
		},
		animationCharacter() {
			lottie.loadAnimation({
				container: this.$refs.mate,
				renderer: 'svg',
				autoplay: true,
				loop: true,
				animationData: require(`@/assets/anims/start_${this.levelSettings.mate}.json`)
			});
			this.animateSlideUp(this.$refs.mate, 400, 800, -80);
		},
		animateSlideUp(element, delay = 0, duration = 800, translateY = 0) {
			element.style.opacity = 0;
			element.style.transform = 'translateY(100px)';
			anime({
				targets: element,
				translateY,
				opacity: 1,
				delay,
				duration
			});
		},
		animateSlideDown(element, delay = 0, complete = null, begin = null) {
			anime({
				targets: element,
				translateY: 100,
				opacity: 0,
				delay,
				duration: 800,
				easing: 'cubicBezier(0.000, 1, 0.030, 1)',
				complete,
				begin
			});
		},
		start() {
			// Stop the current autoplay timer if clicked first
			clearInterval(this.autoPlay.interval);

			const self = this;
			this.$store.dispatch('playSound', 'click');

			this.animateSlideDown(this.$refs.card, 0, null, () => {
				self.$store.dispatch('playSound', 'transition');
			});
			this.animateSlideDown(this.$refs.button.$el);
			this.animateSlideDown(this.$refs.mate, 200, async () => {
				this.$emit('play');
			});
		}
	}
};
</script>

