<!-- eslint-disable vue/no-v-html //-->
<template>
	<div
		:class="[
			`game-result game-result--level_${currentLevel} w-100 h-100 d-flex justify-content-center align-items-center flex-column`
		]"
	>
		<h3 class="game-result__heading mt-4 d-flex flex-column flex-sm-row align-items-center">
			<template v-if="currentScore >= 0">
				You're up
				<ButtonGame
					color="green"
					size="small"
					class="game-result__value ml-3"
				>
					${{ currentScore }}
				</ButtonGame>
			</template>
			<template v-else>
				You're down
				<ButtonGame
					color="red"
					size="small"
					class="game-result__value ml-3"
				>
					${{ currentScore * -1 }}
				</ButtonGame>
			</template>
		</h3>
		<p
			v-if="currentScore >= 0"
			class="game-result__message my-1 my-md-4 px-3"
			v-html="resultCopy('up')"
		/>
		<p
			v-else
			class="game-result__message my-1 my-md-4 px-3"
			v-html="resultCopy('down')"
		/>
		<div class="game-result-again mx-auto my-3 p-3 p-sm-0">
			<h4 class="game-result-again__heading pb-2">
				<template v-if="nextLevel > lastLevel">
					Game over.
				</template>
				<template v-else>
					Wanna keeping playing?
				</template>
			</h4>
			<div class="d-flex flex-column flex-sm-row align-items-center">
				<Button
					v-if="nextLevel > lastLevel"
					size="large"
					class="game-result-again__btn"
					@click.native="reset()"
				>
					Play Again
				</Button>
				<Button
					v-else
					size="large"
					class="game-result-again__btn"
					@click.native="$emit('play')"
				>
					Play Level {{ nextLevel }}
				</Button>
				<Button
					v-if="nextLevel > lastLevel"
					size="large"
					class="game-result-again__close mt-3 mt-sm-0"
					@click.native="$router.push('/')"
				>
					Back to Checkmates
				</Button>
			</div>
		</div>
		<p class="game-result__footer mt-0 mt-md-3">
			Know someone who isn't gambling responsibly?<br />
			Don't wait till they're broke to fix it.
		</p>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import ButtonGame from '@/components/ButtonGame.vue';
import Button from '@/components/Button.vue';

export default {
	name: 'GameStepResult',
	components: { Button, ButtonGame },
	props: {
		playing: {
			type: Boolean
		}
	},
	data() {
		return {
			score: null,
			level: null,
			copy: {
				down: [
					'Gambling debt adds up fast. Even faster if you chase your losses.',
					'When you\'ve lost your stake, it\'s time to tap out.',
					'Quit while you\'re behind. Better to lose a little than the lot.',
					'The longer you gamble, the more you lose.',
					'Losing isn\'t a matter of luck -it\'s a matter of time.',
					'You\'re better off cutting your losses than chasing them.',
					'Most people lose. Winning is the exception, not the rule.',
					'That\'s why you should only gamble with what you\'re willing to lose.',
					'When it\'s game over, you might have lost more than money.',
					'The cost of losing can be more than just money.',
					'A winning streak lasts moments. A losing streak can last a lifetime.',
					'Chasing your losses can result in losing things more valuable than cash.',
					'Money isn\'t the only thing at stake when you gamble.'
				],
				up: [
					'Think you\'ll win again? Fat chance.',
					'You\'ve pulled off a win. Bet you can\'t do it again.',
					'For now. Winning is a matter of luck. Losing is a matter of time.',
					'Few people end up ‘up\'. Quit while you\'re ahead.',
					'You won this time. But what goes up must go down…',
					'You beat the odds. How long until they beat you?',
					'A winning streak lasts moments. A losing streak can last a lifetime.',
					'Winning was hard, right? Unfortunately, losing is easier.',
					'A win doesn\'t make up for lost time.',
					'Winning is knowing when to walk away.',
					'It\'s only a win if you know when to call it quits.',
					'You won this time. But at what price?',
					'How many losses did it take to get the win?',
					'It\'s not a win if those around you lose because of it.',
					'Time. Friends. Family. When it comes to gambling, winning can come at a cost.'
				]
			}
		};
	},
	computed: {
		...mapGetters(['levelSettings', 'lastLevel']),
		currentScore() {
			return this.score === null ? this.$store.state.score : this.score;
		},
		currentLevel() {
			return this.$store.state.level - 1;
		},
		nextLevel() {
			return this.score === null ? this.$store.state.level : this.level;
		}
	},
	watch: {
		playing(newValue, oldValue) {
			if (newValue && !oldValue) {
				// Reset the score when the new level is started.
				this.score = null;
			}
		}
	},
	methods: {
		reset() {
			// Make the deep copy of the score and level
			// Score and level are reseted at the end of the game and therefor this fixes
			// the rewrite of those values when click on "Play Again"
			this.score = JSON.parse(JSON.stringify(this.$store.state.score));
			this.level = JSON.parse(JSON.stringify(this.$store.state.level));
			this.$emit('reset');
		},
		resultCopy(type) {
			const length = this.copy[type].length - 1;
			const step = Math.round(length / 5);
			const from = (step + 1) * (this.currentLevel - 1);
			let to = from + step;
			to = to > length ? length : to;
			to = this.currentLevel === 4 ? length : to;
			const rand = Math.floor(Math.random() * (to - from + 1)) + from;
			return this.copy[type][rand];
		}
	}
};
</script>

