<template>
	<div class="mate-video-outer">
		<div v-if="$route.name === 'video'" :class="['mate-video', {'mate-video--kiosk': $store.getters.isKiosk}]">
			<video v-if="$store.getters.isKiosk" controls autoplay class="w-100">
				<source :src="`videos/video-${$route.params.id}.mp4`" type="video/mp4" />
			</video>
			<iframe
				v-else
				width="100%"
				height="100%"
				:src="`https://www.youtube.com/embed/${videoId}?rel=0`"
				frameborder="0"
				allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
				allowfullscreen
			/>
		</div>
		<div v-if="$route.name === 'fact-sheet'" class="mate-sheet">
			<img :src="`fact-sheets/fact-sheet-${this.$route.params.type}.jpg`" />
		</div>
		<button class="mate-video__close" @click="$router.push(`/${getBacklink()}`)">
			<SvgIcoClose />
		</button>
	</div>
</template>

<script>
import SvgIcoClose from '@/assets/images/ico-close.svg';

export default {
	components: { SvgIcoClose },
	computed: {
		videoId() {
			switch (this.$route.params.id) {
				case 'betty':
					return 'V1H2Y0zN_AY';
				case 'skip':
					return 'mzBjlmdsdzI';
				case 'moby':
					return 'MeMuW3glOKU';
				case 'buck':
				default:
					return 'gjHEQpKXCzA';
			}
		}
	},
	methods: {
		getBacklink() {
			if (this.$route.params && this.$route.params.id) {
				if (this.$route.params.id === undefined || this.$route.params.id === 'undefined') {
					// Precaution on the kiosk.
					return '';
				}

				return this.$route.params.id;
			}

			const back = this.$route.query && this.$route.query.backlink ? this.$route.query.backlink : '';
			if (back === undefined || back === 'undefined') {
				// Precaution on the kiosk.
				return '';
			}

			return back;
		}
	}
};
</script>

