<template>
	<div :class="['bab-game-outer d-flex', `bab-game-outer--level_${$store.state.level}`, {'bab-game-outer--finished': $store.state.finished}]">
		<div class="bab-game d-flex justify-content-center align-items-center">
			<GameIntro
				v-if="$route.name === 'bet-and-bust'"
				@play="$router.push('/bet-and-bust/how-to')"
			/>
			<GameHowToPlay v-if="$route.name === 'bet-and-bust-how-to'" />
			<GamePlay v-if="$route.name === 'bet-and-bust-play'" />
			<SvgIcoAudio :class="['gamer-play__audio', {'gamer-play__audio--inactive': !audio}]" @click="$store.commit('TOGGLE_AUDIO')" />
		</div>
		<button class="bab-game__close" @click="$router.push('/')">
			<SvgIcoClose />
		</button>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';

import GameIntro from '@/components/game/GameIntro.vue';
import GameHowToPlay from '@/components/game/GameHowToPlay.vue';
import GamePlay from '@/components/game/GamePlay.vue';

import SvgIcoAudio from '@/assets/images/ico-audio.svg';
import SvgIcoClose from '@/assets/images/ico-close.svg';

export default {
	name: 'BetAndBust',
	components: { GamePlay, GameHowToPlay, GameIntro, SvgIcoClose, SvgIcoAudio },
	computed: {
		...mapGetters(['audio'])
	},
	mounted() {
		this.$store.commit('SET_SOUND', {
			name: 'level_close',
			path: 'level-close.mp3'
		});

		this.$store.commit('SET_SOUND', {
			name: 'counter',
			path: 'counter.mp3'
		});

		this.$store.commit('SET_SOUND', {
			name: 'click',
			path: 'click.mp3'
		});

		this.$store.commit('SET_SOUND', {
			name: 'transition',
			path: 'transition.mp3'
		});

		['bet', 'bust'].forEach(type => {
			[1, 2, 3, 4, 5].forEach(i => {
				this.$store.commit('SET_SOUND', {
					name: `coins_${type}_${i}`,
					path: `coins-${type}-${i}.mp3`
				});
			});
		});
	}
};
</script>

