}

.bab-game {
	position: relative;
	border: 2px solid white;
	width: 100vw;
	height: 100vh;
	margin: auto;

	@include media-breakpoint-up(lg) {
		width: 996px;
		height: 560px;
	}
}

.bab-game__close {
	position: absolute;
	top: 0;
	right: 0;
	background: none;
	border: none;
	outline: none;
	svg {
		width: 50px;
		height: auto;
		path {
			fill: white;
			transition: all 1s ease;
		}
	}

	@include media-breakpoint-down(md) {
		.bab-game-outer--level_2.bab-game-outer--finished & {
			svg path {
				fill: $blue1;
			}
		}

		.bab-game-outer--level_3.bab-game-outer--finished & {
			svg path {
				fill: $caramel2;
			}
		}

		.bab-game-outer--level_4.bab-game-outer--finished & {
			svg path {
				fill: $steel2;
			}
		}

		.bab-game-outer--level_5.bab-game-outer--finished & {
			svg path {
				fill: $purple2;
			}
		}
	}

	@include media-breakpoint-up(md) {
		svg {
			width: auto;
		}
	}
}

.gamer-play__audio {
	position: absolute;
	bottom: 24px;
	right: 24px;
	@include size(40px);
	cursor: pointer;

	@include media-breakpoint-up(lg) {
		bottom: auto;
		top: 24px;
	}

	&--inactive path {
		fill: $grey1;
	}
}
</style>
