import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/views/Home.vue';
import BetAndBust from '@/views/BetAndBust.vue';
import MateDetail from '@/views/MateDetail.vue';
import MateVideo from '@/views/MateVideo.vue';

Vue.use(Router);

export default new Router({
	routes: [
		{
			path: '/',
			name: 'home',
			component: Home
		},
		{
			path: '/bet-and-bust',
			name: 'bet-and-bust',
			component: BetAndBust
		},
		{
			path: '/bet-and-bust/how-to',
			name: 'bet-and-bust-how-to',
			component: BetAndBust
		},
		{
			path: '/bet-and-bust/rotate-screen',
			name: 'bet-and-bust-rotate-screen',
			component: BetAndBust
		},
		{
			path: '/bet-and-bust/play',
			name: 'bet-and-bust-play',
			component: BetAndBust
		},
		{
			path: '/:id',
			name: 'mate',
			component: MateDetail
		},
		{
			path: '/:id/video',
			name: 'video',
			component: MateVideo
		},
		{
			path: '/fact-sheet/:type',
			name: 'fact-sheet',
			component: MateVideo
		}
	],
	scrollBehavior(to, from) {
		if ([to.name, from.name].includes('fact-sheet')) {
			return {
				selector: '#fact-sheets'
			};
		}

		if (to.hash) {
			return {
				selector: to.hash
			};
		}

		return { x: 0, y: 0 };
	}
});
