<template>
	<div class="game-howto w-100">
		<div class="d-flex flex-column">
			<h3 ref="heading" class="mb-5">
				How to play
			</h3>
			<div ref="btnBet">
				<ButtonGame
					size="small"
					color="green"
					class="game-howto__inline-btn mb-3"
				>
					Bet
				</ButtonGame>
				<p class="mb-5">
					Increase your winnings by hitting BET as many times as you can.
				</p>
			</div>

			<div ref="btnBust">
				<ButtonGame
					size="small"
					color="red"
					class="game-howto__inline-btn mb-3"
				>
					Bust
				</ButtonGame>
				<p class="mb-5">
					But be careful, hitting BUST will cost you big.
				</p>
			</div>
		</div>

		<Button
			ref="btnPlay"
			class="game-howto__next mx-auto mt-3"
			size="large"
			@click.native="goNext()"
		>
			Next
		</Button>
	</div>
</template>

<script>
import anime from 'animejs';
import Button from '@/components/Button.vue';
import ButtonGame from '@/components/ButtonGame.vue';

export default {
	name: 'GameHowToPlay',
	components: { ButtonGame, Button },
	mounted() {
		this.animateCopy('heading', 200, 800);
		this.animateCopy('btnBet', 400, 800);
		this.animateCopy('btnBust', 600, 800);
		this.animateButton();
	},
	methods: {
		animateButton() {
			this.$refs.btnPlay.$el.style.opacity = '0';
			this.$refs.btnPlay.$el.style.transform = 'translateY(100px)';
			return anime({
				targets: this.$refs.btnPlay.$el,
				translateY: 0,
				opacity: 1,
				delay: 900,
				duration: 800
			});
		},
		animateCopy(reference, delay, duration) {
			this.$refs[reference].style.opacity = '0';
			this.$refs[reference].style.transform = 'translateY(100px)';
			return anime({
				targets: this.$refs[reference],
				translateY: 0,
				opacity: 1,
				delay,
				duration
			});
		},
		goNext() {
			const self = this;
			this.$store.dispatch('playSound', 'click');

			// Copy
			anime({
				targets: [this.$refs.heading, this.$refs.btnBet, this.$refs.btnBust, this.$refs.btnPlay.$el],
				translateY: '50px',
				opacity: 0,
				delay: anime.stagger(100),
				duration: 900,
				easing: 'cubicBezier(0.000, 1, 0.030, 1)',
				begin: () => {
					self.$store.dispatch('playSound', 'transition');
				},
				complete: () => this.$router.push('/bet-and-bust/play')
			});
		}
	}
};
</script>

