h3 {
	@include -h5-white-center-bold;
}
p {
	margin: 0 auto;
	padding: 0 1rem;
	@include -body-white-center-regular;
	font-size: 22px;
}
.game-howto__inline-btn {
	margin: 0 auto;
	max-width: 112px;
	height: 48px;
}

.game-howto__next {
	width: 170px;
	height: 45px;
}
</style>
