<!-- eslint-disable new-cap -->
<template>
	<div :class="['game-control', `game-control--${controlPane}`]">
		<fieldset>
			<legend>Popup</legend>
			<div>
				<button @click="forceMate('left')">
					&lt;
				</button>
				<button @click="forceMate('right')">
					&gt;
				</button>
				<button @click="resetMates()">
					Reset
				</button>
			</div>
			<div class="mt-2">
				<small>Show {{ gameSettings.settings.matesPerLevel }} mates, each for
					{{ gameSettings.settings.maxMateShowtime }}s&times;</small><br />
				<small>Showing mate #{{ gameSettings.counters.matesShown }} (out of
					{{ gameSettings.settings.matesPerLevel }}) for
					{{ gameSettings.counters.mateInterval }}s</small><br />
				<small>Chance to show mate is
					{{ Math.round(random * 100 * 100) / 100 }}%</small><br />
				<small>Current probability is
					{{ Math.round(probability * 100 * 100) / 100 }}%</small><br />
			</div>
		</fieldset>
		<fieldset>
			<legend>Game time</legend>
			<div class="mb-2">
				<button v-if="pause" @click="$emit('play')">
					Play
				</button>
				<button v-else @click="$emit('pause')">
					Pause
				</button>
				<button @click="resetTime(0)">
					Reset
				</button>
			</div>
			<div class="mb-2">
				<button @click="resetTime(-10)">
					-10s
				</button>
				<button @click="resetTime(-30)">
					-30s
				</button>
				<button @click="resetTime(10)">
					+10s
				</button>
				<button @click="resetTime(30)">
					+30s
				</button>
			</div>
			<label>Level timer</label>
			<input v-model="timeModel" type="text" />s
		</fieldset>
		<fieldset>
			<legend>Score & Bet/Bust</legend>
			<button @click="resetScore()">
				Reset score
			</button>

			<label class="mt-2">
				Bet/Bust Interval
			</label>
			<span>{{ levelSettings.interval }}</span>ms

			<div class="d-flex flex-row mt-2">
				<div>
					<label>Bet </label>
					<input v-model="betModel" type="text" />
				</div>
				<div>
					<label>Bust </label>
					<input v-model="bustModel" type="text" />
				</div>
			</div>
		</fieldset>
	</div>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';

export default {
	props: {
		probability: {
			type: Number,
			default: 0
		},
		controlPane: {
			type: String,
			default: ''
		},
		pause: {
			type: Boolean
		},
		random: {
			type: Number,
			default: 0
		},
		quotesAvailable: {
			type: Array,
			default: () => []
		},
		maxLevelTime: {
			type: Number,
			default: 0
		}
	},
	computed: {
		...mapGetters(['level', 'score', 'levelSettings', 'gameSettings']),
		betModel: {
			get() {
				return this.levelSettings.bet;
			},
			set(value) {
				value = value === '' ? 0 : value;
				this.$store.commit('CHANGE_BET', parseInt(value, 10));
			}
		},
		bustModel: {
			get() {
				return this.levelSettings.bust;
			},
			set(value) {
				value = value === '' ? 0 : value;
				this.$store.commit('CHANGE_BUST', parseInt(value, 10));
			}
		},
		timeModel: {
			get() {
				return this.gameSettings.counters.time;
			},
			set(value) {
				value = value === '' ? 0 : value;
				this.$store.commit('SET_GAME_COUNTER', { time: value });
			}
		}
	},
	methods: {
		...mapMutations(['SET_GAME_COUNTER', 'SET_GAME_SETTINGS']),
		resetMates() {
			this.$emit('resetMates');
			if (this.quotesAvailable.length === 0) {
				this.$emit('resetQuotes', [...new Array(this.levelSettings.quotes.length).keys()]);
			}

			this.SET_GAME_COUNTER({ timeIntervalLeft: this.gameSettings.counters.time });
			this.SET_GAME_SETTINGS({ maxLevelTime: this.gameSettings.counters.time });
			this.SET_GAME_COUNTER({ matesShown: 0 });
		},
		forceMate(side) {
			this.resetMates();
			this.$emit('resetRandom');
			this.$emit('showMate', side);
		},
		resetTime(add = 0) {
			if (add === 0) {
				this.SET_GAME_COUNTER({ time: this.maxLevelTime });
			} else {
				this.SET_GAME_COUNTER({ time: this.gameSettings.counters.time + add });
			}

			this.SET_GAME_SETTINGS({ maxLevelTime: this.gameSettings.counters.time });
			this.SET_GAME_COUNTER({ timeIntervalLeft: this.gameSettings.counters.time });
		},

		resetScore() {
			this.$store.commit('SET_SCORE', 0);
		}
	}
};
</script>

