<template>
  <header class="site-header d-flex align-items-center">
    <div class="container-fluid p-3 px-lg-5">
      <div class="row align-items-center">
        <div class="col-auto">
          <a href="https://www.responsiblegambling.nsw.gov.au/" target="_blank" class="logo logo-nsw-gov mr-3">
            <img src="https://www.responsiblegambling.nsw.gov.au/__data/assets/git_bridge/0009/271971/templates-2020/images/logo-nsw-gov.svg" alt="" class="img-fluid">
          </a>
        </div>

        <div class="col-auto">
          <router-link to="/" class="logo logo-gaw-2020">
            <img src="https://www.responsiblegambling.nsw.gov.au/__data/assets/git_bridge/0009/271971/templates-2020/images/logo-gaw-2020.svg" alt="" class="img-fluid">
          </router-link>
        </div>

        <div class="col-auto ml-auto d-none d-sm-block">
          <ul class="list-inline mb-0">
            <li class="list-inline-item">
              <a href="#" @click.prevent="facebookShare()" title="Facebook"><img src="https://www.responsiblegambling.nsw.gov.au/__data/assets/git_bridge/0009/271971/templates-2020/images/social-facebook-i.svg" alt="Facebook"></a>
            </li>
            <li class="list-inline-item">
              <a href="#" @click.prevent="twitterShare()" title="Twitter"><img src="https://www.responsiblegambling.nsw.gov.au/__data/assets/git_bridge/0009/271971/templates-2020/images/social-twitter-i.svg" alt="Twitter"></a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </header><!--/ .site-header -->
</template>
<script>
import Button from '@/components/Button.vue';
import SiteLogo from '@/components/SiteLogo.vue';

import SvgIcoFacebook from '@/assets/images/ico-facebook.svg';
import SvgIcoTwitter from '@/assets/images/ico-twitter.svg';
// Import SvgLogoNsw from '@/assets/images/logo-nsw.svg';

export default {
	name: 'SiteHeader',
	components: {
		SiteLogo,
		Button,
		// SvgLogoNsw,
		SvgIcoFacebook,
		SvgIcoTwitter
	},
	methods: {
		getShareData() {
			let copy = 'Check in for Responsible Gambling Awareness Week on the 19–25 of September, 2020. Don\'t wait till they\'re broke to fix it.';
			let url = window.location;

			// eslint-disable-next-line default-case
			switch (this.$route.params.id) {
				case 'buck':
					copy = 'Buck - Check in with the Checkmates - Responsible Gambling Awareness Week 2020';
					url = `${window.location.protocol}//${window.location.host}${window.location.pathname}?share=buck${window.location.hash}`;
					break;
				case 'betty':
					copy = 'Betty - Check in with the Checkmates - Responsible Gambling Awareness Week 2020';
					url = `${window.location.protocol}//${window.location.host}${window.location.pathname}?share=betty${window.location.hash}`;
					break;
				case 'moby':
					copy = 'Moby - Check in with the Checkmates - Responsible Gambling Awareness Week 2020';
					url = `${window.location.protocol}//${window.location.host}${window.location.pathname}?share=moby${window.location.hash}`;
					break;
				case 'skip':
					copy = 'Skip - Check in with the Checkmates - Responsible Gambling Awareness Week 2020';
					url = `${window.location.protocol}//${window.location.host}${window.location.pathname}?share=skip${window.location.hash}`;
					break;
			}

			copy = encodeURI(copy);
			url = encodeURI(url);
			url = url.replace(/#/g, '%23');
			return { url, copy };
		},
		facebookShare() {
			this.popupCenter(`https://www.facebook.com/sharer/sharer.php?u=${this.getShareData().url}`, 'Check in with the Checkmates - Responsible Gambling Awareness Week 2020', 800, 600);
		},
		twitterShare() {
			const { url, copy } = this.getShareData();
			this.popupCenter(`https://twitter.com/intent/tweet?text=${copy}&url=${url}`, copy, 800, 600);
		},
		popupCenter(url, title, w, h) {
			const dualScreenLeft = window.screenLeft === undefined ? window.screenX : window.screenLeft;
			const dualScreenTop = window.screenTop === undefined ? window.screenY : window.screenTop;

			const width = window.outerWidth ? window.outerWidth : document.documentElement.clientWidth ? document.documentElement.clientWidth : screen.width;
			const height = window.outerHeight ? window.outerHeight : document.documentElement.clientHeight ? document.documentElement.clientHeight : screen.height;

			const systemZoom = width / window.screen.availWidth;
			const left = ((width - w) / 2 / systemZoom) + dualScreenLeft;
			const top = ((height - h) / 2 / systemZoom) + dualScreenTop;
			const newWindow = window.open(url, title, `scrollbars=yes, width=${w / systemZoom}, height=${h / systemZoom}, top=${top}, left=${left}`);

			// Puts focus on the newWindow
			if (window.focus) {
				newWindow.focus();
			}
		}
	}
};
</script>
