import Vuex from 'vuex';
import Vue from 'vue';
import ENUM from '@/constants';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		audio: true, // Is the game audio on/off?

		sounds: {}, // List of loaded sounds

		score: 0, // Current score

		level: 1, // Currently played level

		finished: false, // Did we just finished playing

		// Levels setting
		levels: [
			{
				bet: 5,
				bust: 35,
				interval: 900,
				mate: 'buck',
				quotes: [
					{ mate: 'buck', quote: 'Spot us another fiver, mate?' },
					{ mate: 'buck', quote: 'I promise I\'ll pay you back.' },
					{ mate: 'buck', quote: 'C\'mon, you know I\'m good for it.' },
					{ mate: 'buck', quote: 'I\'m feeling lucky.' },
					{ mate: 'buck', quote: 'Can I borrow a couple bucks?' }
				]
			},
			{
				bet: 10,
				bust: 80,
				interval: 800,
				mate: 'betty',
				quotes: [
					{ mate: 'betty', quote: 'If I can just break even, I\'ll be good.' },
					{ mate: 'betty', quote: 'I just need one big win to get ahead.' },
					{ mate: 'betty', quote: 'My luck will change… it has to change...' },
					{ mate: 'betty', quote: 'I\'ve lost too much to stop now…' },
					{ mate: 'betty', quote: 'Going all in will save me — I can feel it.' }
				]
			},
			{
				bet: 20,
				bust: 150,
				interval: 600,
				mate: 'moby',
				quotes: [
					{ mate: 'moby', quote: 'Anyone got a charger?' },
					{ mate: 'moby', quote: 'Wanna know the score?' },
					{ mate: 'moby', quote: 'Brb. the game\'s on.' },
					{ mate: 'moby', quote: 'It\'s not gambling if it\'s a sure thing.' },
					{ mate: 'moby', quote: 'Ooo! Bonus bets!' }
				]
			},
			{
				bet: 50,
				bust: 400,
				interval: 300,
				mate: 'skip',
				quotes: [
					{ mate: 'buck', quote: 'Where\'s Skip?' },
					{ mate: 'betty', quote: 'Skip, where on earth are ya?' },
					{ mate: 'moby', quote: 'Have you seen Skip?' },
					{ mate: 'buck', quote: 'That Skip is a one-man no-show.' }
				]
			}
		],

		// Game settings
		game: {
			// Values, that are in/decreased over time as the timer goes
			counters: {
				time: ENUM.MAX_LEVEL_TIME[0], // Time interval of current level. Goes from MAX -> MIN (e.g. 60s -> 0s)
				timeIntervalLeft: ENUM.MAX_LEVEL_TIME[0],
				mateInterval: 0, // How long have the mate been visible in seconds?
				matesShown: 0 // How many mates did we show so far?
			},
			// Tresholds, max/min values etc.
			settings: {
				maxLevelTime: ENUM.MAX_LEVEL_TIME[0], // What is the maximum time of one level in seconds.
				maxMateShowtime: 11, // How long we want the mate to be visible in seconds?
				matesPerLevel: 2 // How many times we want to show the mate per level
			}
		}
	},
	mutations: {
		SET_SCORE(state, score) {
			state.score = score;
		},
		SET_LEVEL(state, level) {
			state.level = level;
		},
		LEVEL_UP(state) {
			state.level++;
		},
		SCORE_BET(state) {
			state.score += state.levels[state.level - 1].bet;
		},
		SCORE_BUST(state) {
			state.score -= state.levels[state.level - 1].bust;
		},
		CHANGE_BET(state, bet) {
			state.levels[state.level - 1].bet = bet;
		},
		CHANGE_BUST(state, bust) {
			state.levels[state.level - 1].bust = bust;
		},
		SET_SOUND(state, { name, path }) {
			const sfx = new Audio(`${process.env.BASE_URL}mp3/${path}`);
			sfx.preload = true;
			state.sounds[name] = sfx;
		},
		TOGGLE_AUDIO(state) {
			state.audio = !state.audio;
		},
		SET_GAME_COUNTER(state, payload) {
			state.game.counters = { ...state.game.counters, ...payload };
		},
		SET_GAME_SETTINGS(state, payload) {
			state.game.settings = { ...state.game.settings, ...payload };
		},
		SET_FINISHED(state, finished) {
			state.finished = finished;
		}
	},
	actions: {
		playSound({ state, commit }, name) {
			if (!state.audio) {
				return false;
			}

			const prom = state.sounds[name].play();
			if (prom !== undefined) {
				prom.then(_ => {
					// Autoplay started!
				}).catch(_error => {
					commit('TOGGLE_AUDIO');
				});
			}
		}
	},
	getters: {
		level: state => state.level,
		levelSettings: state => state.levels[state.level - 1],
		lastLevel: state => state.levels.length,
		score: state => state.score,
		isDevelopment: _state => process.env.NODE_ENV === ENUM.DEVELOPMENT,
		isKiosk: _state => process.env.VUE_APP_MODE === ENUM.KIOSK,
		audio: state => state.audio,
		gameSettings: state => state.game
	}
});
