<script>
import lottie from 'lottie-web';
import Button from '@/components/Button.vue';
import PlayBox from '@/components/PlayBox.vue';
import SiteHeader from '@/components/SiteHeader.vue';
import SiteFooter from '@/components/SiteFooter.vue';
import KioskFooter from '@/components/KioskFooter.vue';

import SvgIcoBackArrow from '@/assets/images/ico-arrow-back.svg';
import SvgIcoVideoPlay from '@/assets/images/ico-video-play.svg';
import SiteHowTo from '@/components/SiteHowTo.vue';

export default {
	name: 'MateDetail',
	components: {
		Button,
		PlayBox,
		KioskFooter,
		SiteHeader,
		SiteFooter,
		SiteHowTo,
		SvgIcoBackArrow,
		SvgIcoVideoPlay
	},
	filters: {
		capitalize(value) {
			if (!value) {
				return '';
			}

			value = value.toString();
			return value.charAt(0).toUpperCase() + value.slice(1);
		}
	},
	data() {
		return {
			animations: new Array(5),
			buck: {
				blockquote: 'Borrowing money could be a sign that someone\'s gambling is getting out of hand.',
				copy: [
					'<p>Buck\'s a good bloke, who\'s a bit broke.</p>' +
						'<p>Lately, he\'s been borrowing money,<br> from more than one mate.</p>',
					'<p>Maybe his mates are super generous.</p>' +
						'<p>Maybe he always pays them back.</p>' +
						'<p>Maybe he\'s trying to save money (just not his).</p>',
					'<p>It could be nothing.</p>' +
						'<p>Or it could be a sign his gambling\'s<br> getting out of hand.</p>'
				]
			},
			moby: {
				blockquote: 'Having a one-track mind for betting could be a sign that someone\'s gambling is getting out of hand.',
				copy: [
					'<p>Moby\'s a close mate, who\'s closer with his phone.</p>' +
						'<p>Lately, he\'s been checking the odds. A lot.</p>',

					'<p>Maybe he\'s not betting that much.<p>' +
						'<p>Maybe he\'s just checking the weather.</p>' +
						'<p>Maybe he\'s looking for a different type of <em>match</em>.</p>',

					'<p>It could be nothing.</p>' +
						'<p>Or it could be a sign his gambling\'s<br> getting out of hand.</p>'
				]
			},

			betty: {
				blockquote: 'Chasing losses could be a sign that someone\'s gambling is getting out of hand.',
				copy: [
					'<p>Betty\'s a champ who\'s battling a bit of a losing streak.</p>' +
						'<p>Lately, she\'d rather chase her losses than cut them.</p>',

					'<p>Maybe she\'s actually <em>up</em>.</p>' +
						'<p>Maybe she\'s got everything under control.' +
						'<p>Maybe <em>danger</em> is her middle name.</p>',

					'<p>It could be nothing.</p>' +
						'<p>Or it could be a sign her gambling\'s<br> getting out of hand.</p>'
				]
			},

			skip: {
				blockquote: 'Skipping social events could be a sign that someone\'s gambling is getting out of hand.',
				copy: [
					'<p>Skip\'s a fun guy to be around, when he\'s around.</p>' +
						'<p>Lately, he hasn\'t been showing up to the usual events.</p>',

					'<p>Maybe he\'s just got a lot on his plate.</p>' +
						'<p>Maybe he\'s found cooler friends.</p>' +
						'<p>Maybe he\'s secretly fighting crime.</p>',

					'<p>It could be nothing.</p>' +
						'<p>Or it could be a sign his gambling\'s<br> getting out of hand.</p>'
				]
			}
		};
	},
	computed: {
		blockquote() {
			return this[this.$route.params.id].blockquote;
		}
	},
	updated() {
		this.loadAnimations();
	},
	mounted() {
		this.loadAnimations();
	},
	methods: {
		copy(id) {
			return this[this.$route.params.id].copy[id];
		},
		loadAnimations() {
			this.animations.forEach(anim => {
				anim.destroy();
			});
			this.animations = [];

			Object.keys(this.$refs).forEach(reference => {
				const m = reference.match(/__(\d)$/);
				if (m) {
					const index = m[1];
					this.animations.push(lottie.loadAnimation({
						container: this.$refs[reference],
						renderer: 'svg',
						autoplay: true,
						loop: true,
						animationData: require(`@/assets/anims/detail_${this.$route.params.id}_${index}.json`)
					}));
				}
			});
		}
	}
};
</script>

