<template>
	<div class="gamer-play position-relative w-100 h-100">
		<GameStepResult
			:playing="playing"
			@play="initNewLevel()"
			@reset="resetGame()"
		/>
		<transition name="close-curtain">
			<div
				v-if="!$store.state.finished"
				:class="[`game-inner game-inner--level_${level}`]"
			>
				<GameStepStart
					v-if="!playing"
					:show-counter="level === 1 && !isDevelopment"
					@play="startPlaying()"
				/>
				<GameStepLevels
					v-if="playing"
					:level="level"
					@finished="levelHasFinished()"
				/>
			</div>
		</transition>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';

import GameStepResult from '@/components/game/steps/GameStepResult.vue';
import GameStepStart from '@/components/game/steps/GameStepStart.vue';
import GameStepLevels from '@/components/game/steps/GameStepLevels.vue';

export default {
	name: 'GamePlay',
	components: { GameStepLevels, GameStepStart, GameStepResult },
	data() {
		return {
			playing: false // Is the game running currently?
		};
	},
	computed: {
		...mapGetters(['level', 'isDevelopment'])
	},
	mounted() {
		this.resetGame();
	},
	methods: {
		levelHasFinished() {
			this.$store.dispatch('playSound', 'level_close');
			this.$store.commit('SET_FINISHED', true);
			this.$store.commit('LEVEL_UP');
		},

		initNewLevel() {
			this.playing = false;
			this.$store.commit('SET_FINISHED', false);
		},

		startPlaying() {
			this.playing = true;
		},

		// Let's play again from the beginning
		resetGame() {
			this.$store.commit('SET_LEVEL', 1);
			this.$store.commit('SET_SCORE', 0);
			this.$store.commit('SET_FINISHED', false);
			this.playing = false;
		}
	}
};
</script>

<style lang="scss" scoped>
.game-inner {
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100%;
	max-height: 100%;
	overflow: hidden;

	transition: all 1s ease-in-out;

	&--level_1 {
		background: $blue1 url(~@/assets/images/bg-game.svg?external) 0 0 repeat;
	}
	&--level_2 {
		background: $carmel1 url(~@/assets/images/bg-game.svg?external) 0 0
			repeat;
	}
	&--level_3 {
		background: $steel1 url(~@/assets/images/bg-game.svg?external) 0 0
			repeat;
	}
	&--level_4 {
		background: $purple1 url(~@/assets/images/bg-game.svg?external) 0 0
			repeat;
	}
}

.close-curtain {
	transition: all 1s ease-in-out;

	&-enter,
	&-leave-to {
		top: 50%;
		min-height: 0px;
		max-height: 0px;
	}
}
</style>
