<!-- eslint-disable new-cap -->
<template>
	<div class="game-step-levels w-100">
		<div
			class="d-flex justify-content-around justify-content-sm-between align-items-center pb-5 pb-sm-0 pb-md-5"
		>
			<div
				class="score d-flex flex-column flex-sm-row align-items-center"
			>
				<span class="score__title mr-0 mr-sm-4">
					Score
				</span>
				<ButtonGame
					:color="score >= 0 ? 'green' : 'red'"
					class="score__value"
					size="small"
				>
					${{ score }}
				</ButtonGame>
			</div>
			<div class="time d-flex flex-column flex-sm-row align-items-center">
				<span class="time__title mr-0 mr-sm-4">
					Time
				</span>
				<span
					class="time__value d-flex align-items-center justify-content-center"
				>{{ gameSettings.counters.time }}</span>
			</div>
		</div>

		<div class="game-button position-relative">
			<div ref="bubbles" />
			<ButtonGame
				v-if="counter > 0"
				color="yellow"
				size="large"
				class="game-button__btn game-button__btn--counter mt-5"
			>
				<template v-if="counter === 2">
					Ready
				</template>
				<template v-else>
					Go!
				</template>
			</ButtonGame>
			<ButtonGame
				v-else
				:color="bet ? 'green' : 'red'"
				size="large"
				class="game-button__btn mt-5"
				@click.native="betOrBust()"
			>
				<template v-if="bet">
					Bet
				</template>
				<template v-else>
					Bust
				</template>
			</ButtonGame>
		</div>

		<Transition
			name="t"
			@after-enter="mateBubble = true"
			@after-leave="mateBubble = false"
		>
			<div v-if="mate !== ''" :class="[`game-mate game-mate--${side}`]">
				<Transition name="t-bubble">
					<div v-if="mateBubble" class="game-mate__bubble">
						<span>{{ quote }}</span>
					</div>
				</Transition>
				<SvgPopupBuck
					v-if="mate === 'buck'"
					class="game-mate__character"
				/>
				<SvgPopupBetty
					v-if="mate === 'betty'"
					class="game-mate__character"
				/>
				<SvgPopupMoby
					v-if="mate === 'moby'"
					class="game-mate__character"
				/>
				<SvgPopupSkip
					v-if="mate === 'skip'"
					class="game-mate__character"
				/>
			</div>
		</Transition>

		<button
			v-if="isDevelopment"
			class="settings"
			@click="controlPane = controlPane === '' ? 'active' : ''"
		>
			🛠︎
		</button>
		<ControlPane
			v-if="isDevelopment"
			v-bind="{probability, controlPane, pause, random, quotesAvailable, maxLevelTime}"
			@resetMates="hideMate()"
			@resetRandom="random = 0"
			@showMate="side => showMate(side)"
			@resetQuotes="q => quotesAvailable = q"
			@pause="pause = true"
			@play="pause = false"
		/>
	</div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import lottie from 'lottie-web';

import ControlPane from '@/components/game/ControlPane.vue';
import ButtonGame from '@/components/ButtonGame.vue';
import ENUM from '@/constants';

import SvgPopupBuck from '@/assets/images/popup-buck.svg';
import SvgPopupBetty from '@/assets/images/popup-betty.svg';
import SvgPopupMoby from '@/assets/images/popup-moby.svg';
import SvgPopupSkip from '@/assets/images/popup-skip.svg';

export default {
	name: 'GameStepLevels',
	components: {
		ControlPane,
		ButtonGame,
		SvgPopupBuck,
		SvgPopupBetty,
		SvgPopupMoby,
		SvgPopupSkip
	},
	data() {
		return {
			bet: true, // If false, then BUST
			side: ENUM.EMPTY, // Left or Right if we want to show a mate
			mate: ENUM.EMPTY, // What mate to display
			mateBubble: false,
			random: Math.random(),
			pause: true, // Pause on init.
			counter: 2, // Display initial counter?
			quotesAvailable: [],
			quote: '',
			controlPane: '' // Open settings pane?
		};
	},
	computed: {
		...mapGetters(['level', 'score', 'levelSettings', 'gameSettings', 'isDevelopment']),
		maxLevelTime() {
			return ENUM.MAX_LEVEL_TIME[this.level - 1];
		},
		probability() {
			if (
				this.pause ||
				this.gameSettings.counters.mateInterval > 0 ||
				this.gameSettings.counters.matesShown >= this.gameSettings.settings.matesPerLevel
			) {
				return 0;
			}

			const step = this.gameSettings.counters.timeIntervalLeft % this.gameSettings.counters.time;
			const timePerAvatar =
				(this.gameSettings.settings.maxLevelTime - (this.gameSettings.settings.maxMateShowtime * this.gameSettings.settings.matesPerLevel)) /
				this.gameSettings.settings.matesPerLevel;
			const probabilityPerStep = 1 / timePerAvatar;

			return step * probabilityPerStep;
		}
	},
	created() {
		this.quotesAvailable = [
			...new Array(this.levelSettings.quotes.length).keys()
		];
		this.SET_GAME_COUNTER({ time: this.maxLevelTime });
		this.SET_GAME_COUNTER({ timeIntervalLeft: this.maxLevelTime });
		this.SET_GAME_COUNTER({ mateInterval: 0 });
		this.SET_GAME_COUNTER({ matesShown: 0 });
		this.SET_GAME_SETTINGS({ maxLevelTime: this.maxLevelTime });
	},
	async mounted() {
		const self = this;

		const counterInterval = setInterval(() => {
			self.counter -= 1;
			if (self.counter === 0) {
				self.pause = false;
				clearInterval(counterInterval);
			}
		}, 1000);

		setInterval(() => {
			if (self.pause) {
				return;
			}

			self.bet = Math.random() <= 0.5;
		}, self.levelSettings.interval);

		await new Promise(resolve => {
			const timer = setInterval(() => {
				if (self.pause) {
					return;
				}

				if (
					self.mate === ENUM.EMPTY &&
					self.probability >= self.random
				) {
					self.showMate(Math.random() < 0.5 ? ENUM.LEFT : ENUM.RIGHT);
				}

				if (self.mate !== ENUM.EMPTY) {
					this.SET_GAME_COUNTER({ mateInterval: this.gameSettings.counters.mateInterval + 1 });
				}

				if (
					self.gameSettings.counters.mateInterval >= self.gameSettings.settings.maxMateShowtime
				) {
					self.hideMate();
				}

				this.SET_GAME_COUNTER({ time: this.gameSettings.counters.time - 1 });
				if (self.gameSettings.counters.time <= 0) {
					resolve();
					window.clearInterval(timer);
				}
			}, 1000);
		});

		this.$emit('finished');
	},
	methods: {
		...mapMutations(['SET_GAME_COUNTER', 'SET_GAME_SETTINGS']),
		randomInt(min, max) {
			return Math.floor(Math.random() * (max - min + 1)) + min;
		},
		betOrBust() {
			if (this.gameSettings.counters.time <= 0) {
				return;
			}

			if (this.bet) {
				this.coinsAnimation();
				this.$store.dispatch(
					'playSound',
					`coins_bet_${this.randomInt(1, 5)}`
				);
				this.$store.commit('SCORE_BET');
			} else {
				this.coinsAnimation();
				this.$store.dispatch(
					'playSound',
					`coins_bust_${this.randomInt(1, 5)}`
				);
				this.$store.commit('SCORE_BUST');
			}
		},
		coinsAnimation() {
			const container = document.createElement('div');
			container.classList.add('game-button__bubble');
			container.classList.add(
				`game-button__bubble--${Math.floor(Math.random() * (4 - 1)) +
					1}`
			);
			this.$refs.bubbles.append(container);
			const anim = lottie.loadAnimation({
				container,
				renderer: 'svg',
				autoplay: true,
				loop: false,
				animationData: require(`@/assets/anims/${
					this.bet ? 'bet' : 'bust'
				}-bubble_level-${this.level}.json`)
			});
			anim.onComplete = () => {
				anim.destroy();
			};
		},
		showMate(side) {
			if (this.gameSettings.counters.matesShown < this.gameSettings.settings.matesPerLevel) {
				const key = Math.floor(
					Math.random() * this.quotesAvailable.length
				);
				const quoteKey = this.quotesAvailable[key];
				this.quote = this.levelSettings.quotes[quoteKey].quote;
				this.mate = this.levelSettings.quotes[quoteKey].mate;
				this.quotesAvailable.splice(key, 1);

				this.side = side;
				this.SET_GAME_COUNTER({ matesShown: this.gameSettings.counters.matesShown + 1 });
			}
		},
		hideMate() {
			this.side = ENUM.EMPTY;
			this.mate = ENUM.EMPTY;
			this.SET_GAME_COUNTER({ mateInterval: 0 });
			this.SET_GAME_COUNTER({ timeIntervalLeft: this.gameSettings.counters.time });
			this.random = Math.random();
		}
	}
};
</script>

